* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.nav {
    background-color: aqua;
    display: flex;
    justify-content: space-between;
   padding: 10px 20px
}

.logo {
    width: 20px;
}

.nav--list {
    display: grid;
    grid-template-columns: repeat(2, 100px);
}

.nav--list > li {
    list-style-type: none;
}

.nav--link {
    color: purple;
    text-decoration: none;
}

.bag {
    width: 40px;
    height: 35px
}